div.tfw-layout-tabstrip {
  position       : relative;
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items    : stretch;
          align-items    : stretch;
}
div.tfw-layout-tabstrip > header {
  -webkit-flex           : 0 0 auto;
          flex           : 0 0 auto;
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : row;
          flex-direction : row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items    : stretch;
          align-items    : stretch;
  margin         : 0 0 -1px;
  z-index        : 1;
}
div.tfw-layout-tabstrip > header > div.void {
  -webkit-flex         : 1 1 auto;
          flex         : 1 1 auto;
  border-bottom: 1px solid var(--thm-black);
}
div.tfw-layout-tabstrip > header > button {
  cursor          : pointer;
  color           : var(--thm-bgP);
  -webkit-font-feature-settings    : "c2sc";
          font-feature-settings    : "c2sc";
  font-variant    : small-caps;
  font-weight     : bold;
  background-color: transparent;
  display         : -webkit-flex;
  display         : flex;
  -webkit-justify-content : center;
          justify-content : center;
  -webkit-align-items     : center;
          align-items     : center;
  border          : none;
  min-height      : 2em;
  max-height      : 2em;
  height          : 2em;
  border-bottom   : 1px solid var(--thm-black);
  margin          : 0;
  padding         : 0 .5em;
}
div.tfw-layout-tabstrip > header > button:active,
div.tfw-layout-tabstrip > header > button:hover {
  color: var(--thm-bgPL);
}
div.tfw-layout-tabstrip > header > button.selected {
  border       : 1px solid var(--thm-black);
  border-bottom: 1px solid var(--thm-bg2);
  z-index      : 2;
  overflow     : hidden;
  border-radius: 2px 2px 0 0;
}
div.tfw-layout-tabstrip > div {
  position  : relative;
  -webkit-flex      : 1 1 auto;
          flex      : 1 1 auto;
  margin    : 0;
  padding   : .5em;
  border    : 1px solid var(--thm-black);
  border-top: none;
  z-index   : 0;
}
button.tfw-view-checkbox {
    padding: 0;
    margin: 2px;
    border: none;
    background: transparent;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
}

button.tfw-view-checkbox.wide {
    display: -webkit-flex;
    display: flex;
    width: 100%;
}

button.tfw-view-checkbox>label {
    -webkit-order: 1;
            order: 1;
    opacity: .6;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    text-align: left;
    transition: opacity .2s;
    cursor: pointer;
}

button.tfw-view-checkbox.reverse>label {
    -webkit-order: -1;
            order: -1;
}

button.tfw-view-checkbox>div.pin {
    position: relative;
    width: 34px;
    height: 20px;
    overflow: visible;
    -webkit-flex-grow: 0;
            flex-grow: 0;
    margin: 2px 8px 2px 2px;
    -webkit-order: 0;
            order: 0;
}

button.tfw-view-checkbox>div.pin>div.bar {
    content: ".";
    font-size: 0;
    position: absolute;
    left: 0;
    top: 3px;
    width: 34px;
    height: 14px;
    border-radius: 7px;
}

button.tfw-view-checkbox>div.pin>div.btn {
    content: ".";
    font-size: 0;
    position: absolute;
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    border-radius: 10px;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    transition: opacity .2s, -webkit-transform .2s;
    transition: transform .2s, opacity .2s;
    transition: transform .2s, opacity .2s, -webkit-transform .2s;
}

button.tfw-view-checkbox.ok>label {
    opacity: 1;
}

button.tfw-view-checkbox.ok>div.pin>div.btn {
    -webkit-transform: translateX(14px);
            transform: translateX(14px);
}

button.tfw-view-checkbox>header.hide {
    display: none;
}
